import React, { useState } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import "./createAcc.css";
import { Link } from "react-router-dom";

export default function CreateAcc() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [createdAcc, setCreatedAcc] = useState("");

  function userMessage(flag) {
    if (flag == "true") {
      alert("Account Created");
    }
    else {
      alert("Failed: Username already exists");
    }
  }

  function redirect(flag) {
    if (flag == "true") {
      window.location.href='/login';
    }
  }

  function validateForm() {
    return username.length > 0 && password.length > 0;
  }

  function handleSubmit(event) {
    event.preventDefault();
  }

  return (
    <div align="center" className="Login">
    <h1>Create a new account : </h1>
      <Form onSubmit={handleSubmit}>
        <Form.Group size="lg" controlId="username">
          <Form.Label>Username</Form.Label>
          <Form.Control
            autoFocus
            type="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </Form.Group>
        <Form.Group size="lg" controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </Form.Group>
        <Button block size="lg" type="submit" disabled={!validateForm()}
         onClick={() => {
                    fetch("/create_acc/" + username + "!" + password)
                        .then(response => 
                            response.json()
                        )
                        .then(data => {
                            setCreatedAcc(data.createdAcc)
                            userMessage(data.createdAcc)
			    redirect(data.createdAcc)
                        })
                        .catch(error => {
                            console.log(error)
                        })
                        }}
        >
          Create Account
        </Button>
        <li>
          <Link to = "/">Back</Link>
        </li>
      </Form>
    </div>
  );
}